service.interceptors.response.use(
  response => {
    return response
  },
  error => {
    //tokenstatus500
    if (error.response) {
      switch (error.response.code) {
        case 401:
          router.push({path: '/login'})
        case 403:
          router.push({path: '/login'})
        case 404:
          router.push({path: '/login'})
      }
      switch (error.response.status) {
        case 401:
            router.push({path: '/login'})
          // router.replace({
          //   path: 'login',
          //   // query: { redirect: router.currentRoute.fullPath } // path
          // })
        // case 500:
        //     router.push({path: '/login'})
          // router.replace({
          //   path: 'login',
          //   // query: { redirect: router.currentRoute.fullPath } // path
          // })
        case 403:
          router.push({path: '/login'})
        case 404:
          router.push({path: '/login'})
      }
    }
    return Promise.reject(error.response)
  }
)

